// Componente de animação para elementos que entram na tela
const AnimatedSection = ({ children, delay = 0 }) => {
  const controls = useAnimation();
  const [ref, inView] = useIntersectionObserver({
    threshold: 0.1,
    triggerOnce: true,
  });

  useEffect(() => {
    if (inView) {
      controls.start('visible');
    }
  }, [controls, inView]);

  return (
    <motion.div
      ref={ref}
      animate={controls}
      initial="hidden"
      variants={{
        visible: {
          opacity: 1,
          y: 0,
          transition: {
            duration: 0.8,
            delay,
            ease: [0.25, 0.46, 0.45, 0.94]
          }
        },
        hidden: {
          opacity: 0,
          y: 50
        }
      }}
    >
      {children}
    </motion.div>
  );
};

// Componente de progresso de habilidades
const SkillProgress = ({ skill, level, icon: Icon }) => {
  const [progress, setProgress] = useState(0);
  const [ref, inView] = useIntersectionObserver({
    threshold: 0.5,
    triggerOnce: true,
  });

  useEffect(() => {
    if (inView) {
      const timer = setTimeout(() => setProgress(level), 500);
      return () => clearTimeout(timer);
    }
  }, [inView, level]);

  return (
    <Box ref={ref} sx={{ mb: 3 }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
        <Icon sx={{ mr: 1, color: 'primary.main' }} />
        <Typography variant="body1" sx={{ fontWeight: 600 }}>
          {skill}
        </Typography>
        <Typography variant="body2" sx={{ ml: 'auto', color: 'text.secondary' }}>
          {level}%
        </Typography>
      </Box>
      <LinearProgress
        variant="determinate"
        value={progress}
        sx={{
          height: 8,
          borderRadius: 4,
          backgroundColor: 'rgba(0,0,0,0.1)',
          '& .MuiLinearProgress-bar': {
            borderRadius: 4,
            background: 'linear-gradient(45deg, #6200EE 30%, #BB86FC 90%)',
          }
        }}
      />
    </Box>
  );
};

